// src/components/TutorialModal.tsx
import React, { useRef, useState, useEffect } from "react";
import styled from "styled-components";
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination } from "swiper/modules";
import type { Swiper as SwiperType } from "swiper"; //  

import "swiper/css";
import "swiper/css/pagination";

const Overlay = styled.div`
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.75);
  backdrop-filter: blur(4px);
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 375px;
`;

const ModalBox = styled.div`
  width: 90%;
  max-width: 520px;
  background: #fdfdfd;
  color: #111;
  padding: 2.4rem 1.6rem;
  border-radius: 16px;
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.25);
  position: relative;
  overflow: hidden;
`;

const SlideCard = styled.div`
  background: #fff;
  border: 1px solid #eee;
  border-radius: 12px;
  padding: 1.5rem;
  min-height: 240px;
  display: flex;
  flex-direction: column;
  justify-content: center;
`;

const TutoTitle = styled.h2`
  margin-bottom: calc(30 / 480 * 100%);
  font-weight: 600;
  color: #333;
`;

const TutoTxt = styled.p`
  color: #444;
  white-space: pre-line;
  position: relative;
  padding-left: calc(12 / 480 * 100%);
  margin-bottom: calc(10 / 480 * 100%);

  &::before {
    content: "";
    position: absolute;
    left: 0;
    color: #aaa;
  }
`;

const FlexBox = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: calc(30 / 480 * 100%);
  gap: calc(30 / 480 * 100%);
`;

const NavBtn = styled.button`
  background: #333;
  color: #fff;
  border: none;
  padding: calc(16 / 480 * 100%) calc(32 / 480 * 100%);
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.2s;

  &:hover {
    background: #555;
  }
`;

const CloseBtn = styled.button`
  margin-top: calc(32 / 480 * 100%);
  background: transparent;
  border: 2px solid #333;
  color: #333;
  padding: calc(14 / 480 * 100%) calc(30 / 480 * 100%);
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.2s;

  &:hover {
    background: #f1f1f1;
  }
`;

interface Props {
  onClose: () => void;
}

const TutorialModal: React.FC<Props> = ({ onClose }) => {
  const swiperRef = useRef<SwiperType | null>(null);
  const [activeIndex, setActiveIndex] = useState(0);
  const modalRef = useRef<HTMLDivElement | null>(null);

  const handleNext = () => {
    swiperRef.current?.slideNext();
  };

  const handlePrev = () => {
    swiperRef.current?.slidePrev();
  };

  useEffect(() => {
    const handleOutsideClick = (e: MouseEvent) => {
      if (modalRef.current && !modalRef.current.contains(e.target as Node)) {
        onClose();
      }
    };
    document.addEventListener("mousedown", handleOutsideClick);
    return () => document.removeEventListener("mousedown", handleOutsideClick);
  }, []);

  return (
    <Overlay>
      <ModalBox className="font-20" ref={modalRef}>
        <Swiper
          spaceBetween={100}
          slidesPerView={1}
          pagination={{ type: "fraction" }}
          modules={[Pagination]}
          onSwiper={(swiper: SwiperType) => {
            swiperRef.current = swiper;
          }}
          onSlideChange={(swiper: SwiperType) =>
            setActiveIndex(swiper.activeIndex)
          }
        >
          <SwiperSlide>
            <SlideCard>
              <TutoTitle className="font-24">  </TutoTitle>
              <TutoTxt className="font-20">
                   .
              </TutoTxt>
              <TutoTxt className="font-20">
                    .
              </TutoTxt>
              <TutoTxt className="font-20">
                     .
              </TutoTxt>
              <TutoTxt className="font-20">  !</TutoTxt>
            </SlideCard>
          </SwiperSlide>
          <SwiperSlide>
            <SlideCard>
              <TutoTitle className="font-24"> ? </TutoTitle>
              <TutoTxt className="font-20">  ,</TutoTxt>
              <TutoTxt className="font-20">
                        
                .
              </TutoTxt>
              <TutoTxt className="font-20">
                   ?     .
              </TutoTxt>
              <TutoTxt className="font-20">
                         .
              </TutoTxt>
              {/*   */}
            </SlideCard>
          </SwiperSlide>
          <SwiperSlide>
            <SlideCard>
              <TutoTitle className="font-24">  </TutoTitle>
              <TutoTxt className="font-20">
                     .
              </TutoTxt>
              <TutoTxt className="font-20">
                    .
              </TutoTxt>
            </SlideCard>
          </SwiperSlide>
        </Swiper>

        <FlexBox style={{ marginTop: "20px" }}>
          {activeIndex > 0 && <NavBtn onClick={handlePrev}> </NavBtn>}
          {activeIndex < 2 && <NavBtn onClick={handleNext}> </NavBtn>}
        </FlexBox>
        <CloseBtn onClick={onClose}></CloseBtn>
      </ModalBox>
    </Overlay>
  );
};

export default TutorialModal;
